@import 'variables';

/* BUTTONS */
.button {
  border-radius: 10px;
  padding: 10px;
  font-weight: bold;
  color: $background-color;
  background-color: $accent-color-light;
  cursor: pointer;
  border: none;
  transition: opacity 250ms;

  &:hover, &:focus {
    opacity: 0.8;
  }
}