@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;700&display=swap);
.button{border-radius:10px;padding:10px;font-weight:bold;color:#f5f5f5;background-color:#3fc1c9;cursor:pointer;border:none;transition:opacity 250ms}.button:hover,.button:focus{opacity:0.8}#nav-container{background-color:#f5f5f5;opacity:0.8;display:flex;flex-direction:row;justify-content:space-between;position:fixed;top:0;width:100%}#img-container{width:50%;text-align:left}#header-img{width:150px;height:50px}#nav-bar{font-size:1.5em;width:50%;text-align:right}.nav-link{margin:40px;text-decoration:none;color:#364f6d;transition:color 250ms}.nav-link:hover,.nav-link:focus{color:#3fc1c9}#pricing{display:flex;flex-direction:row}#pricing-option-1,#pricing-option-2,#pricing-option-3{display:flex;flex-direction:column;border-style:solid;border-color:#e6e6e6;border-radius:25px;padding:25px;margin:10px;width:30%;height:600px}.pricing-option-description{text-align:left}.pricing-option-bulletpoints,#additional-info{text-align:left}.select-option{margin-top:auto}footer{text-align:right;padding:0 20px 0 20px;margin:0 20px 0 20px;color:#f5f5f5;background-color:#3fc1c9}ul{list-style-type:circle}#form{display:flex;flex-direction:column;align-items:center}#email{font-size:1.2em;padding:10px}#submit{font-size:1.2em;margin:15px}#body-text{text-align:center}.features-title,.reviews-title,.additional-info-header,.pricing-option-header,#title-container{color:#364f6d}#reviews{font-style:italic}#video{width:560px;height:315px;border:none}@media (max-width: 450px){#nav-container{font-size:0.8em}}@media (max-width: 700px){main{width:100%;margin:0 auto}#img-container{width:10%}#header-img{width:90px;height:30px}#nav-bar{margin:0px}#video{width:100%}#pricing-option-1,#pricing-option-2,#pricing-option-3{height:auto;width:80%}#pricing{flex-direction:column;align-items:center}footer{padding:0 10px 0 10px;margin:0 10px 0 10px}}@media (max-width: 800px){#nav-bar{width:80%;font-size:1em;text-align:right}.nav-link{margin:10px}#pricing-option-1,#pricing-option-2,#pricing-option-3{height:auto}}@media (max-width: 1250px){main{width:100%;margin:0 auto}#nav-bar{width:80%;font-size:1em}#pricing-option-1,#pricing-option-2,#pricing-option-3{height:auto}}body{text-align:center;padding-top:40px;font-family:"Mulish",serif;background-color:#f5f5f5}main{width:75%;margin:0 auto}

